.full{
   background-color: $color-primary;
}
.full-light{
   background-color: $color-light;
}
.full-limit{
   padding-left: 88px;
   padding-right: 88px;
   @include large-screen-ls {
      padding-left: 52px;
      padding-right: 52px;
   }
   @include medium-screen-ms {
      padding-left: 39px;
      padding-right: 39px;
   }
   @include small-screen-ss {
      padding-left: 15px;
      padding-right: 15px;
   }
}

.container{
   max-width: 1110px;
   margin: 0 auto;
}

@include large-screen-ls {
   .container{
      max-width: 960px;
      margin: 0 auto;
   }
}

@include medium-screen-ms {
   .container{
      max-width: 720px;
      margin: 0 auto;
   }
}

@include small-screen-ss {
   .container{
      max-width: 540px;
      margin: 0 auto;
   }
}