//typeface
@font-face {
   font-family: 'Fat';
   src: url(../fonts/NotoSansTC-Bold.otf);
   unicode-range: U+4E00-9FFF, U+3000-301E, U+FF01-FF1F, U+0030-0039;
}
@font-face {
   font-family: 'Fat';
   src: url(../fonts/Roboto-Bold.ttf);
   unicode-range: U+00-024F;
}
@font-face {
   font-family: 'Regular';
   src: url(../fonts/NotoSansTC-Regular.otf);
   unicode-range: U+4E00-9FFF, U+3000-301E, U+FF01-FF1F, U+0030-0039;
}
@font-face {
   font-family: 'Regular';
   src: url(../fonts/Roboto-Regular.ttf);
   unicode-range: U+00-024F;
}
@font-face {
   font-family: 'Medium';
   src: url(../fonts/NotoSansTC-Medium.otf);
   unicode-range: U+4E00-9FFF, U+3000-301E, U+FF01-FF1F, U+0030-0039;
}
@font-face {
   font-family: 'Medium';
   src: url(../fonts/Roboto-Medium.ttf);
   unicode-range: U+00-024F;
}
@font-face {
   font-family: 'BlackItalic';
   src: url(../fonts/Roboto-BlackItalic.ttf);
   unicode-range: U+00-024F;
}
.f1{
   font-family: 'Fat';
}
.f2{
   font-family: 'Regular';
}
.f3{
   font-family: 'Medium';
}
.f4{
   font-family: 'BlackItalic';
}

//text-style
h1, h2, h3, h4, h5, h6{
   line-height: 1.3;
}
.h1{
   font-size: 64px;
}
.h2{
   font-size: 48px;
}
.h3{
   font-size: 36px;
}
.h4{
   font-size: 32px;
}
.h5{
   font-size: 24px;
}
.h6{
   font-size: 20px;
}
.h7{
   font-size: 16px;
}
.h8{
   font-size: 14px;
}


//color
$white: #ffffff;
$color-primary: #AA0601;
$color-light: #AA06010D;
$color-dark: #650300;
$color-info: #212529;
$color-other: #707070;
$color-border-line: #DEE2E6;

//text-color
.text-content {
   color: $color-primary;
}
.title-color {
   color: $color-dark;
}
.text-info {
   color: $color-info;
}
.text-span {
   color: $color-other;
}
.text-white {
   color: $white;
}





