.banner {
   height: 840px;
   background: url(../images/banner.png) center no-repeat;
   background-size: cover;
   @include small-screen-ss {
      height: 394px;
   }
}
.banner-inner {
   height: 100%;
}

.specialty-card {
   max-width: 20%;
   margin-bottom: $base*10;
   figure{
      width: 100px;
      height: 100px;
      margin: 0 auto 30px;
      position: relative;
   }
   img{
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto;
   }
   h4{
      text-align: center;
      color: $color-dark;
      margin-bottom: $base;
   }
   p{
      text-align: justify; //文字間距對齊
      text-justify: distribute; //IE用
   }
   @include medium-screen-ms {
      max-width: 40%;
      figure{
         margin: 0 auto $base*4;
      }
      h4{
         margin-bottom: $base*2;
      }
   }
   @include small-screen-ss {
      max-width: 70%;
   }
}
.specialty-order {
   @include medium-screen-ms {
      order: 3;
   }
}

.product-card {
   li {
      display: flex;
      align-items: center;
      margin-bottom: $base*8;
      &:nth-child(2){
         flex-direction: row-reverse;
      }
   }
   img {
      // width: 635px;
      width: 50%;
   }
   h4 {
      position: relative;
      &:nth-child(2){
         margin-right: 30px;
      }
   }
   a {
      margin-left: 86px;
      &::before {
         width: 48px;
         height: 48px;
         position: absolute;
         background-size: contain;
      }
   }
   
   @include medium-screen-ms {
      li{
         margin-bottom: $base*2;
         &:nth-child(2){
            flex-direction: row;
         }
      }
      // img {
      //    width: 390px;
      // }
      h4 {
         font-size: 28px;
         &:nth-child(2){
            margin-right: 0;
         }
      }
      a {
         margin-left: 70px;
            &::before {
            width: 32px;
            height: 32px;
         }
      }
      
   }
   @include small-screen-ss {
      li {
         position: relative;
      }
      img {
         width: 100%;
      }
      h4 {
         position: absolute;
         top: 50%;
         left: 45%;
         transform: translate(-50%, -50%);
      }
      a {
         color: $white;
      }
   }
}
.diamond::before {
   content: "";
   background: url(../images/icon-diamond.svg) center no-repeat;
   top: 5px;
   left: 30px;
   @include medium-screen-ms {
      top: 0;
   }
   @include small-screen-ss {
      background: url(../images/icon-diamond-white.svg) center no-repeat;
   }
}
.sun::before {
   content: "";
   background: url(../images/icon-sun.svg) center no-repeat;
   top: 5px;
   left: 30px;
   @include medium-screen-ms {
      top: 0;
   }
   @include small-screen-ss {
      background: url(../images/icon-sun-white.svg) center no-repeat;
   }
}
.thunder::before {
   content: "";
   background: url(../images/icon-thunder.svg) center no-repeat;
   top: 8px;
   left: 30px;
   @include medium-screen-ms {
      top: 3px;
   }
   @include small-screen-ss {
      background: url(../images/icon-thunder-white.svg) center no-repeat;
   }
}