.d-flex{
   display: flex;
}
.flex-jc-end{
   justify-content: flex-end;
}
.flex-jc-between{
   justify-content: space-between;
}
.flex-jc-around{
   justify-content: space-around;
}
.flex-ai-center{
   align-items: center;
}
.flex-wrap{
   flex-wrap: wrap;
}


.text-center {
   text-align: center;
}
.text-right {
   text-align: right;
}


.hover-bg-color:hover{
   @include hover-bg-color;
}
.hover-underline:hover{
   @include hover-underline;
}
.hover-transform:hover{
   @include hover-transform;
}
.line-bottom{
   border-bottom: 1px solid $white;
}


$base: 4px;

.pt-16{
   padding-top: $base*16;
}
.pb-10{
   padding-bottom: $base*10;
}
.pb-12{
   padding-bottom: $base*12;
}

.mb-1{
   margin-bottom: $base;
}
.mb-4{
   margin-bottom: $base*4;
}
.mb-5{
   margin-bottom: $base*5;
}
.mb-6{
   margin-bottom: $base*6;
}
.mb-8{
   margin-bottom: $base*8;
}
.mb-15{
   margin-bottom: $base*15;
}

.ml-3{
   margin-left: $base*3;
}
.ml-5{
   margin-left: $base*5;
}
.ml-8{
   margin-left: $base*8;
}
.ml-9{
   margin-left: $base*9;
}
.ml-15{
   margin-left: $base*15;
}
.mr-12{
   margin-right: $base*12;
}


@include medium-screen-ms {
   .width-ms-6{
      width: $base*6;
   }
   .width-ms-8{
      width: $base*8;
   }
   
   .f3-ms{
      font-family: 'Medium';
   }
   
   .h2-ms{
      font-size: 48px;
   }
   .h4-ms{
      font-size: 32px;
   }
   .h6-ms{
      font-size: 20px;
   }
   
   .pt-ms-10{
      padding-top: $base*10;
   }
      .pb-ms-8{
      padding-bottom: $base*8;
   }

   .mb-ms-2{
      margin-bottom: $base*2;
   }
   .mb-ms-6{
      margin-bottom: $base*6;
   }
   .mb-ms-8{
      margin-bottom: $base*8;
   }

   .ml-ms-0{
      margin-left: 0;
   }
   .ml-ms-6{
      margin-left: $base*6;
   }
   .mr-ms-10{
      margin-right: $base*10;
   }
}



@include small-screen-ss {
   .display-none-ss{
      display: none;
   }
   .flex-column-ss{
      flex-direction: column;
   }
   .flex-ai-start-ss{
      align-items: start;
   }


   .width-ss-5{
      width: $base*5;
   }

   .h4-ss{
      font-size: 32px;
   }
   .h5-ss{
      font-size: 24px;
   }
   .h7-ss{
      font-size: 16px;
   }
   .h8-ss{
      font-size: 14px;
   }

   .pt-ss-8{
      padding-top: $base*8;
   }
   .pb-ss-6{
      padding-bottom: $base*6;
   }

   .mb-ss-2{
      margin-bottom: $base*2;
   }
   .mb-ss-5{
      margin-bottom: $base*5;
   }
   .ml-ss-0{
      margin-left: 0;
   }
   .ml-ss-1{
      margin-left: $base;
   }
   .my-ss-3{
      margin: $base*3 0;
   }
}