.navbar{
   padding: 0 88px;
   text-align: center;
   @include large-screen-ls {
      padding: 0 39px;
   }
   @include small-screen-ss {
      padding: 0;
   }
}
.text-hide{
   width: 80px;
   height: 49px;
   display: block;
   background: url(../images/logo.png) center no-repeat;
   @include text-hide;
}
.hover-bg-color {
   padding: 40px 32px;
   @include medium-screen-ms {
      padding: 40px 20px;
   }
   @include small-screen-ss {
      width: 100%;
      padding: 8px 15px;
   }
}

.navbar-menu{
   li{
      @include small-screen-ss {
         width: 50%;
         border: 1px solid $white;
      }
   }
}

.footer-navbar{
   padding: 36px 88px 29px;
   @include large-screen-ls {
      padding: 40px 39px;
   }
   @include small-screen-ss {
      padding: 24px 15px 22px;
   }
}
.copyright-ss{
   @include small-screen-ss {
      position: relative;
   }
}
.footer-logo {
   @include medium-screen-ms{
      display: none;
   }
   @include small-screen-ss {
      width: 46px;
      display: block;
      position: absolute;
      right: 0;
      bottom: 0;
   }
}
